
.primary{
    display: inline-flex;
}

label {
    display: flex;
    justify-content: center;
}
.registration{
    padding-top: 40px;
    padding-bottom: 51px;
}

.form-part {
    position: relative;
    @media screen and (max-width: 767px){
    text-align: center;
    max-width: 290px;
    border-radius: 50% 50% 0px 0px;
    margin-left: auto;
    margin-right: auto;
    background-color: #FFFFF9;
    box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15), 4px 4px 4px rgba(171, 129, 108, 0.15);
    }
}

 .form-part__image,
 .form-part__image--confetti{
    position: absolute;
    z-index: -1;
 }

 .form-part__image{

    @media screen and (min-width: 768px) and (max-width: 1279px) {
        left: 0px;
        top:24px;
        transform: rotate(69.49deg);
        }

    @media screen and (min-width: 1280px) {
    left: 69;
    top:24px;
    transform: rotate(69.49deg);
    }
}

.donut{
    transform: rotate(-69.49deg);
 }

 .form-part__image--confetti{
    @media screen and (min-width: 1280px) {
   top:50px;
   left: 600px;
    }

   @media screen and (min-width: 768px) and (max-width: 1279px) {

   bottom:0px;
   left: 600px;
 }
}


.form-part__bg{
    margin-right: auto;
    margin-left: auto;

    @media screen and (min-width: 768px) {
    justify-content: space-between;
    display: flex;
    flex-wrap: nowrap;
    background-color: #FFFFF9;
    box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15), 4px 4px 4px rgba(171, 129, 108, 0.15);
    }
}



@media screen and (min-width: 768px) and (max-width: 1279px){
    .form-part__bg{
    max-width: 634px;
    border-radius: 150px;
    }
}

@media screen and (min-width: 1280px){
    .form-part__bg{
    max-width: 822px;
    height: 260px;
    border-radius: 150px;
}
}



@media screen and (min-width: 768px)  {
.form-part__wrapper {
    padding-top: 35px;
    padding-bottom: 35px;
    display: flex;
    flex-direction: column;
    flex-basis: calc((50% - 45px) / 2);

    &:nth-child(3){
        padding: 0;
        margin-top: auto;
        margin-bottom: auto;
        }
    }
}

@media screen and (max-width: 767px)  {
    .form-part__wrapper:nth-child(2) {
    margin-bottom: 25px;
    }
}

.form-part__title{
    text-align: center;
    font-weight: 400;

    @media screen and (max-width: 767px){
    font-family: 'Caveat Brush', cursive;
    font-size: 24px;
    line-height: 2.71; 
    margin-bottom: 11px; 
    }

    @media screen and (min-width: 768px) and (max-width: 1279px){
    font-family: 'Caveat Brush', cursive;
    font-size: 24px;
    line-height: 2.71; 
    margin-bottom: 10px;
    }
    @media screen  and (min-width: 1280px) {
    font-family: 'Caveat Brush', cursive; 
    font-size: 36px;
    line-height: 1.81;
    }
}

.info-form{
    @media screen and (max-width: 767px){
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    max-width: 265px;
    margin-bottom: 11px;
    border-bottom: 1px solid var(--marker-color);
    }

    @media screen and (min-width: 768px) and (max-width: 1279px){
    margin: 0;
    padding-top: 19px;
    padding-right: 30px;
    padding-left: 10px;
    border-right: 1px solid var(--marker-color);
    height: 190px;
    }
    @media screen and (min-width: 1280px){
    padding-top: 19px;
    padding-left: 107px; 
    padding-right: 30px;
    border-right: 1px solid var(--marker-color);
    height: 190px;
}
}

.info-form__input{
    border: none;
}

.info{
    flex-direction: column;
}

.info__item{
    @media screen and (max-width: 767px) {
    padding-bottom: 25px;

    &:last-child{
    padding-bottom: 20px;
    }
}
    @media screen and (min-width: 768px) and (max-width: 1279px){
    text-align: right;
    padding-bottom: 34px;
    }
    @media screen and (min-width: 1280px){
    text-align: right;
    padding-bottom: 30px;
    }
}

.info__text{
    font-weight: 500;  
    @media screen  and (max-width: 1279px){
    font-size: 18px;
    line-height: 1.22;
    }
    @media screen  and (min-width: 1280px){
    font-size: 20px;
    line-height: 1.22; 
}
}

.checkin-form__field:first-child .checkin-form__input{
    @media screen and (min-width: 1280px){
    padding-top: 14px;
    }
    @media screen and (min-width: 768px) and (max-width: 1279px){
    padding-top: 18px;
    }
}

.checkin-form__input{
    margin: 0;
    border: none;
    border-bottom: 1px solid var(--marker-color);
    padding-top: 16px;
    background-color: #FFFFF9;

    @media screen and (min-width: 768px) and (max-width: 1279px){
        width: 234px;
    }

    &::placeholder{
        font-size: 16px;
        line-height: 1.22;
        color: rgba(171, 129, 108, 0.5);
        }
}


.checkin-form__text{
    font-size: 16px;
    line-height: 1.22;

    @media screen  and (min-width: 768px)and (max-width: 1279px){
    padding-top: 19px;
    }
    @media screen  and (min-width: 1280px){
    font-size: 18px;
    padding-top: 19px;
    }
}


